.page {
  scroll-behavior: smooth;

    &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s;

    &:target {
      opacity: 1;
      pointer-events: all;

    }
  }
}
